<template>
    <a :href="link" target="_blank" class="item">
        <div class="item-image">
            <img :src="'http://bulgarif.temp.swtest.ru/'+image" :alt="name"
                         @mouseenter="atMouseEnter($event.target)"
                         @mouseleave="atMouseLeave($event.target)" 
                         :style="'transform: translateY(-' + offset + 'px)'"> 
        </div>
        <div class="item-content">
            <p class="item-title">{{name}}</p>
            <div class="item-tags">
                <i v-for="item in tagsIcon" :key="item.name" :class="item.icon">
                    <span>{{item.name}}</span>
                </i>
            </div>
        </div>
    </a>
</template>

<script>
    export default {
        props: ['name', 'link', 'image', 'tags'],
        data() {
            return {
                offset: 0
            }
        },
        methods: {
            atMouseLeave(el) {
                this.offset = 0;
            },
            atMouseEnter(el) {
                this.offset = el.height - 300;
                    
            }
        },
        computed: {
            tagsIcon() {
                let tagsArr = [];
                this.tags.forEach((element, index) => {
                    let icon;
                    switch (element) {
                        case "html":
                            icon = "fab fa-html5";
                            break;
                        case "css":
                            icon = "fab fa-css3-alt";
                            break;
                        case "js":
                            icon = "fab fa-js";
                            break;
                        case "php":
                            icon = "fab fa-php";
                            break;
                        case "vue.js":
                            icon = "fab fa-vuejs";
                            break;
                    }
                    tagsArr.push({
                        "name": element,
                        "icon": icon
                    })
                });
                return tagsArr;
            }
        }
    }
</script>

<style scoped>
    .item {
        margin-bottom: 80px;
        border-radius: .5px;
        cursor: pointer;
        display: block;
        color: #000;
        transition: all .2s ease;
        background-color: #e2e3e5;
    }
    .item:hover {
        box-shadow: rgba(0, 0, 0, 0.2) 10px 10px 0px 0px;
        text-decoration: none;
        color: #ef4056;
        transition: all .2s ease;
    }
    img {
        max-width: 100%;
        height: auto;
    }
    .item-image {
        max-height: 300px;
        position: relative;
        overflow: hidden;
    }
    .item-image img:hover {
        transition: transform 7s ease;
    }
    .item-image img {
        transition: transform 2s ease;
    }
    .item-content {
        position: relative;
        padding: 30px;
        background-color: #e2e3e5;
    }

    .item-title {
        font-size: 18px;
        line-height: 20px;
        font-weight: bold;
    }
    .item-tags {
        background-color: #ffe484;
        position: absolute;
        left: 15px;
        bottom: -15px;
        padding: 3px 15px 0px;
    }
    .item-tags i {
        font-size: 24px;
        margin: 0 5px;
        color: #ef4056;
    }
    .item-tags i:hover {
        color: #000;
        cursor: pointer;
    }
    .item-tags i span{
        color: #000;
        display: inline-block;
        vertical-align: middle;
        margin-left: 3px;
        font-size: 16px;
    }
</style>



