<template>
    <div class="container">
        <div class="row" v-masonry transition-duration="0.5s" item-selector=".item-wrap" horizontal-order="true">
                <div v-masonry-tile class="col-lg-4 col-md-6 col-12 item-wrap" v-for="item in items" :key="item._id">
                    <item :name="item.name"
                        :link="item.link"
                        :tags="item.technologies"
                        :image="item.image.path"
                    >
                    </item>
            </div>
        </div>
    </div>
</template>

<script>
    import Vue from 'vue';
    import {mapGetters} from 'vuex';
    import item from "./Item.vue";
    import {VueMasonryPlugin} from 'vue-masonry';
    Vue.use(VueMasonryPlugin);

    export default {
        components: {
            item
        },
        computed: {
            ...mapGetters({
				items: 'getItems'
			}),
        }
    }
</script>


