<template>
    <div>
        <div class="loader" v-if="isLoading">
            <img src="img/loader.gif" alt="">
        </div>
        <div class="container text-center  py-5 ">
            <div class="logo-wrap">
                <a href="/" class="logo d-inline-block">
                    <img src="img/avatar.jpg" alt="photo">
                </a>
                    <transition-group appear tag="ul" class="contacts-list">
                        <li v-for="(item, index) in contacts" :key="item.name" class="contact-item" :style="'transition-delay:' + index + 's'">
                            <a :href="item.link" target="_blank">
                                <i :class="item.icon"></i>
                            </a>
                        </li>
                    </transition-group>
            </div>
            <div class="row d-flex justify-content-center">
                <div class="col-lg-8 col-12">
                    <div class="alert alert-secondary" role="alert">
                        <vue-typed-js :strings="entryText" :contentType="'html'">
                            <span class="typing"></span>
                        </vue-typed-js>
                    </div>
                </div>
            </div>
            <div class="row">
                <div class="container text-center">
                    
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import Vue from 'vue';
    import VueTypedJs from 'vue-typed-js';

    Vue.use(VueTypedJs);

    export default {
        data() {
            return {
                entryText: ["Hello, world! I'm junior web developer from Kazan, Russia.",
                            "And there are some of my works..."
                            ],
                filtersList: [{
                        name: "html",
                        icon: "html5"
                    },
                    {
                        name: "css",
                        icon: "css3-alt"
                    },
                    {
                        name: "css",
                        icon: "css3-alt"
                    },
                    {
                        name: "js",
                        icon: "js"
                    },
                    {
                        name: "php",
                        icon: "php"
                    },
                    {
                        name: "vue",
                        icon: "vuejs"
                    }],
                    contacts: [
                        {
                          name: "Telegram",
                          link: "https://t-do.ru/bu1at",
                          icon: "fab fa-telegram"
                        },
                        {
                          name: "Github",
                          link: "https://github.com/bulich", 
                          icon: "fab fa-github"
                        },
                        {
                          name: "Email",
                          link: "mailto:garifb@gmail.com" ,
                          icon: "fas fa-at"
                        }
                    ]
            }
        },
        computed: {
            isLoading() {
                return this.$store.getters.getStatus;
            }
        }
        
    }
</script>

<style>
    .logo-wrap {
        display: inline-block;
        position: relative;
        padding: 0 20px 20px;
    }
    .contacts-list {
        list-style: none;
        padding-left: 0;
        margin-bottom: 0;
    }
    .contact-item {
        display: inline-block;
        margin: 0 15px;
        position: relative;
        transition-duration: .5s;
        transition-timing-function: ease;
        transition-property: opacity;
    }
    .contact-item a i {
        color: #000;
        font-size: 24px;
    }
    .contact-item a:hover i {
        color: #ef4056;
    }
    .contacts-list .contact-item:first-child,
    .contacts-list .contact-item:last-child {
        top: -20px
    }
    .contact-item.v-enter {
        opacity: 0;
    }
    .contact-item.v-enter-to {
        opacity: 1;
    }
    .loader {
        position: fixed;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        background-color: #fff;
        z-index: 10;
    }
    .loader img {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
    }
</style>



