import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

export const store = new Vuex.Store({
  state: {
    items: [],
    loading: true,
    baseUrl: 'http://bulgarif.temp.swtest.ru/api/collections/get/works?token=ec0f59f5e55f887e2ecb4d11ebb512',
    itemsNumber: 0,
    itemLimit: 6
  },
  getters: {
    getItems(state) {
      return state.items;
    },
    getStatus(state) {
      return state.loading;
    }
  },
  mutations: {
    loadItems(state, payload) {
      state.items = state.items.concat(payload.entries);
      state.loading = false;
      state.itemLimit = 3;
      state.itemsNumber += 3;
    },
    changeStatus(state) {
      state.loading = true;
    },
    changeSkip(state) {
      state.itemsNumber = 3;
    }
  },
  actions: {
    loadItems({commit, state}) {
      commit('changeStatus');
      axios({
        method: 'post',
        url: state.baseUrl,
        headers: { 'Content-Type': 'application/json' },
        data: {
          limit: state.itemLimit,
          skip: state.itemsNumber
        }
      })
      .then(res => commit('loadItems', res.data));
    },
    changeSkip({commit}) {
      commit('changeSkip');
    }
  }
});



